    let utils = require('@/util/utils.js');
    let request = utils.request;
	export default {
		data() {
			return {
				popup: true,
				// titleHeight:"136",
				textHeight:"136",
				// 巡检标题
				title:"",
				titleFoc:false,
				// 巡检描述
				desc:"",
				descFoc:false,
				// // 通知部门
				// departmentList:[],
				// // 通知人员
				// person:[],
				// 图片上传列表
				imgNumber:9,
				upImgIndex:0,
				upImgList:[],
				
				// 重复提交
				btnClick:true
			}
		},
        computed:{
            departmentList(){
                return this.$store.state.market.addMarket.departmentList;
            },
            person(){
                let person = this.$store.state.market.addMarket.person;
                let leaderlist = person.leaderlist,
                    deptuserlist = person.deptuserlist,
                    allPerson = [];
                console.log(this.$store.state.market.addMarket.person)
                for(let item of deptuserlist){
                    for(let ite of item.userlist){
                        allPerson.push(ite)
                    }
                }
                for(let item of deptuserlist){
                    allPerson.push(item)
                }
                return allPerson;
            }
        },
		onShow:function(){
            this.$forceUpdate();
		},
		methods: {
            // 重置表单内容
            resetForm:function(){
                let departmentList = this.departmentList;
                for (let item of departmentList) {
                    item.select = false;
                }
                this.$store.commit("setAddDepart", departmentList);
                
                let person = this.$store.state.market.addMarket.person;
                let leaderlist = person.leaderlist,
                    deptuserlist = person.deptuserlist;
                // leader
                for (let item of leaderlist) {
                    item.select = false;
                }
                // deptuserlist
                for (let item of deptuserlist) {
                    for (let itm of item.userlist) {
                        itm.select = false;
                    }
                }
                person = {
                    leaderlist,
                    deptuserlist
                }
                this.$store.commit("setAddPerson", person);
            },
            sortArr: function(arr){            
                for(var i=0; i<arr.length; i++){
                    for(var j=i+1; j<arr.length; j++){
                        if(arr[i].deptid == arr[j].deptid){
                            arr.splice(j,1);
                            j--;
                        }
                    }
                }
                return arr;
            },
			add:function(){
				if(!this.btnClick) {return;}
				this.btnClick = false;
				uni.showLoading({
					title:"正在添加巡检...",
					mask:true
				})
				let _this = this;
				let option = {
					loginspection: {
						inspectionlogid: 0,
						inspectionid: 0,
						typedesc: "", // 设备 电梯 F1
						deptid: uni.getStorageSync("userinfo").deptid, // 登录人
						deptname: uni.getStorageSync("userinfo").deptname, // 部门名称
						title: this.title, // 标题
						insertdate: "", // 提交时间
						inspectionplace: "", // 位置
						remark: this.desc, // 描述
						status: 0, // 状态
						usernumber: uni.getStorageSync("userinfo").usernumber, // 操作人工号
						username: uni.getStorageSync("userinfo").username, // 名称
						lstupdatedate: "",
						lstuserid: "",
						other1: "",
						other2: "",
						confirmuserid: "",
						confirmdate: "",
						loginspectionimg: [ // 图片
							// {
							// 	inspectionimgid: 0,
							// 	inspectionlogid: 0,
							// 	imgurl: "string", // 地址
							// 	uploadtime: "" // 上传时间
							// }
						],
						loginspectionquestion: [ // 讨论
						// 	{
						// 		questionid: 0,
						// 		inspectionlogid: 0,
						// 		reason: "string", // 原因
						// 		remark: "string", // 解决办法
						// 		insertdate: "",
						// 		userid: "string", // 登录人id
						// 		username: "string", // 名称
						// 		deptid: 0, // 部门
						// 		deptname: "string" // 部门名称
						// 	}
						],
						loginspectiontype: [ //问题归属
						// 	{
						// 		loginspectiontypeid: 0,
						// 		inspectionlogid: 0,
						// 		inspectionid: 0,
						// 		inspectioncode: "string", // 编码
						// 		inspectionname: "string", // 名称
						// 		insertdate: ""
						// 	}
						],
						mapinspectiondept: [ //通知部门
							// {
							// 	mapinspectiondeptid: 0,
							// 	inspectionlogid: 0,
							// 	deptid: 1, // 部门id
							// 	deptname: "", // 名称
							// 	insertdate: ""
							// }
						],
						mapinspectionuser: [ //通知人员
							// {
							// 	miuid: 0,
							// 	inspectionlogid: 0,
							// 	userid: 0, // id
							// 	usernumber: "", // 工号
							// 	username: "", // 名称
							// 	mobile: "", // 手机
							// 	deptid: 0, // id
							// 	deptname: "", // 名称
							// 	insertdate: ""
							// }
						]
						}
				}
                // 人员
                console.log(option)
                let person = [];
				for(let item of this.person){
                    if(item.select == true){
                        person.push(item)
                        let obj = {
                            miuid: 0,
                            inspectionlogid: 0,
                            userid: item.userid, // id
                            usernumber: item.usernumber, // 工号
                            username: item.username, // 名称
                            mobile: "", // 手机
                            deptid: item.deptid, // id
                            deptname: item.deptname, // 名称
                            insertdate: ""
                        }
                        option.loginspection.mapinspectionuser.push(obj)
                    }
				}
                // 部门
                let deptlist = [];
                for(let item of this.departmentList){
                    if(item.select == true){
                        deptlist.push(item);
                    }
                }
                deptlist = this.sortArr(deptlist.concat(person));
				console.log(deptlist)
				for(let item of deptlist){
					let obj = {
						mapinspectiondeptid: 0,
						inspectionlogid: 0,
						deptid: item.deptid, // 部门id
						deptname: item.deptname, // 名称
						insertdate: ""
					}
					option.loginspection.mapinspectiondept.push(obj)
				}
				for(let item of this.upImgList){
					let obj = {
						inspectionimgid: 0,
						inspectionlogid: 0,
						imgurl: item.path, // 地址
						uploadtime: "" // 上传时间
					}
					option.loginspection.loginspectionimg.push(obj)
				}
				// if(!this.title){
				// 	uni.hideLoading();
				// 	this.btnClick = true;
				// 	uni.showModal({
				// 		title:"",
				// 		content:"请填写巡检标题！",
				// 		showCancel:false,
				// 		success:()=>{
				// 			_this.titleFoc = true
				// 		}
				// 	})
				// 	return;
				// }
				if(!this.desc){
					uni.hideLoading()
					this.btnClick = true;
					uni.showModal({
						title:"",
						content:"请填写巡检描述！",
						showCancel:false,
						success:()=>{
							_this.descFoc = true
						}
					})
					return;
				}
				// let nitice = false;
				if(!option.loginspection.mapinspectiondept.length && !option.loginspection.mapinspectionuser.length){
					uni.hideLoading()
					this.btnClick = true;
					uni.showModal({
						title:"",
						content:"请选择通知部门！",
						showCancel:false
					})
					return;
				}
				if(!option.loginspection.mapinspectionuser.length && !option.loginspection.mapinspectiondept.length){
					uni.hideLoading()
					this.btnClick = true;
					uni.showModal({
						title:"",
						content:"请选择通知人员！",
						showCancel:false
					})
					return;
				}
				console.log("开始添加巡检记录",option);
				console.log("开始添加巡检记录JSON",JSON.stringify(option));
				request.addDetail(option).then(data => {
					uni.hideLoading();
					let [err,res] = data;
					console.log("添加结果：",err,data);
					if(err == null && res.data.success){
						// 重置
						uni.showToast({
							icon:"success",
							title:"添加成功"
						})
						this.btnClick = true;
						uni.navigateBack({
							delta:1
						})
					}else{
						this.btnClick = true;
						uni.showToast({
							icon:"none",
							duration:3000,
							title:res.data.errmsg
						})
						return;
					}
				});
			},
			addImg:function(){
				utils.addImg(this);
			},
			delImg:function(index){
				utils.delImg(index,this);
			},
			addFrom:function(type){
				console.log("添加图表");
				uni.navigateTo({
					url:"../addSelect/addSelect?type=" + type
				})
			}
		},
		onHide: function() {
			console.log("add onHide")
		},
		onUnload: function() {
			console.log("add onUnload")
			this.$store.commit("changeTabbar", 2);
			// 重置
            this.resetForm();
		}
	}
